



export  function addXRGizmoManager(BABYLON,scene){
    if(BABYLON==null || scene==null){
        console.log("Gizmo manager needs BABYLON import and a scene")
        return null;
    }

    var gizmoManager = new BABYLON.GizmoManager(scene);
    //gizmoManager.boundingBoxGizmoEnabled = true;
    //gizmoManager.boundingBoxDragBehavior = null;
    gizmoManager.usePointerToAttachGizmos = false;
    gizmoManager.clearGizmoOnEmptyPointerEvent = true;
    gizmoManager.rotationSphereSize = 0.04;
    gizmoManager.scaleBoxSize = 0.08;
    

        function selectMode(mode){
            gizmoManager.positionGizmoEnabled = false;
            gizmoManager.rotationGizmoEnabled = false;
            gizmoManager.scaleGizmoEnabled = false;
            gizmoManager.boundingBoxGizmoEnabled = false;

            switch(mode){
                case 0:
                    gizmoManager.boundingBoxGizmoEnabled= true;
                    gizmoManager.gizmos.boundingBoxGizmo.setEnabledScaling(false);
                    
                    break;
                case 1:
                    gizmoManager.positionGizmoEnabled = true;
                    break;
                case 2:
                    gizmoManager.rotationGizmoEnabled = true;
                    break;
                case 3:
                    gizmoManager.scaleGizmoEnabled = true;
                    break;
                
            }

        }
        gizmoManager.selectMode=selectMode;

    return gizmoManager;
} 