
import * as faceapi from 'face-api.js'

import { firestore } from '../firebase'


const MODEL_URL = '/models'

var currentUser = {};

var fullFaceDescriptions = {};

var image = new Image();

var isloaded = false;


export function setCurrentUser(user) {
    currentUser = user;
}

export function getData() {
    return fullFaceDescriptions;
}

export function getImage() {
    return image;
}

export function isLoaded() {
    return isloaded;
}


export async function loadModels() {
    //await faceapi.loadModels(MODEL_URL)
    await faceapi.loadFaceDetectionModel(MODEL_URL)
    await faceapi.loadFaceLandmarkModel(MODEL_URL)
    await faceapi.loadFaceRecognitionModel(MODEL_URL)
    await faceapi.loadFaceExpressionModel(MODEL_URL)

    await faceapi.nets.tinyFaceDetector.loadFromUri(MODEL_URL).then(() => {
        isloaded = true;
    })

}



export function drawHTMLImage(canvas, image, width, height) {
    const ctx = canvas.getContext("2d");
    ctx.drawImage(image, 0, 0, width, height);
}


export async function getFullFaceDescription(canvas) {
    //this.fullFaceDescriptions = await faceapi.allFaces(canvas, minConfidence);
    return fullFaceDescriptions = await faceapi.detectAllFaces(canvas,
        new faceapi.TinyFaceDetectorOptions()).withFaceLandmarks().withFaceExpressions();

}

export function forEachdrawDescription(canvas) {
    fullFaceDescriptions.forEach((fd, i) => {
        //faceapi.drawLandmarks(canvas, fd.landmarks, { drawLines: false })
    })
}

export function drawDescription(canvas) {
    //const resizedDetections = faceapi.resizeResults(detections,displaysize)
    faceapi.draw.drawDetections(canvas, fullFaceDescriptions)
    faceapi.draw.drawFaceExpressions(canvas, fullFaceDescriptions)

}


export function DoProcess(picture) {

    image.src = picture;
}



export function setEmotionData(emotiondata, tag) {


    if (currentUser && emotiondata != undefined) {

        var tempData = JSON.parse(JSON.stringify(emotiondata));

        var dateObj = new Date();
        var month = dateObj.getUTCMonth() + 1; //months from 1-12
        var day = dateObj.getUTCDate();
        var year = dateObj.getUTCFullYear();
        var hours = dateObj.getUTCHours();
        var minutes = dateObj.getUTCMinutes();

        var addtag = (tag+ " - " + year + "-" + month + "-" + day + " " + hours + ":" + minutes).toString();
        firestore.collection('users').doc(currentUser.uid).collection('emotion_capture').doc(addtag).set(tempData);
    }


}

export function actionButton(takeCapture) {
    takeCapture().then(() => {
        var image = getImage();
        if (isLoaded) {
            getFullFaceDescription(image).then((result) => {
                try {
                    setEmotionData(result[0].expressions, "test")
                } catch (error) {
                    console.log(error)
                }

            })
        }


    })
}

export function TakeEmotionByTime(takeCapture, timeout, tagname) {
    setTimeout(() => {
        takeCapture().then(() => {
            var image = getImage();
            if (isLoaded) {
                getFullFaceDescription(image).then((result) => {
                    try {
                        setEmotionData(result[0].expressions, tagname)
                    } catch (error) {
                        console.log(error)
                    }

                })
            }


        })

    }, timeout);

}

export function TakeEmotion(takeCapture, tagname) {
    takeCapture().then(() => {
        var image = getImage();
        if (isLoaded) {
            getFullFaceDescription(image).then((result) => {
                try {
                    setEmotionData(result[0].expressions, tagname)
                } catch (error) {
                    console.log(error)
                }

            })
        }


    })
}

