            function exercise1Logic() {


                var carmodel = scene.getMeshByName("carBox1")


                //Impulse Settings
                var impulseDirection = new BABYLON.Vector3(0, 0, 1);
                var impulseMagnitude = 5;
                var contactLocalRefPoint = BABYLON.Vector3.Zero();


                var obj_Distance = 1;
                var obj_Time = 1
                var obj_Velocity = 1;
                var obj_Accel = 1

                var display_Distance = 0;
                var display_Time = 0;
                var display_Velocity = 0;
                var display_Accel = 0




                var disposable_ghosts = []
                var disposable_lines = []

                function activate() {

                    if (disposable_ghosts) {
                        disposable_ghosts.forEach(item => {
                            item.dispose();
                        })
                        disposable_ghosts = []


                    }

                    if (disposable_lines) {
                        disposable_lines.forEach(item => {
                            item.dispose();
                        })
                        disposable_lines = []
                    }
                    //fireball(obj_Velocity)

                    limitTime(carmodel);



                }




                function limitTime(mesh) {



                    function ghosting(mesh, data) {


                        var ghost_sphere = BABYLON.MeshBuilder.CreateSphere('ghost_sphere', { segments: 6, diameter: 0.1 }, scene);
                        ghost_sphere.visibility = 0.5;
                        ghost_sphere.position = mesh.position;
                        disposable_stuff.push(ghost_sphere)
                        disposable_ghosts.push(ghost_sphere)
                        section_infodisplay.logicManager.AddDisposableStuff(ghost_sphere)




                        function ghostLine() {
                            if (disposable_ghosts.length > 1) {
                                var past_sphere = disposable_ghosts[disposable_ghosts.length - 2]
                                var actual_sphere = disposable_ghosts[disposable_ghosts.length - 1]
                               
                                var line = new BABYLON.Mesh.CreateLines("line", [past_sphere.getAbsolutePosition(), actual_sphere.getAbsolutePosition()], scene, true);
                                disposable_lines.push(line);
                                disposable_stuff.push(line);
                                section_infodisplay.logicManager.AddDisposableStuff(line)

                            }

                        }


                        function ghostData(mesh) {
                            var miniplame = BABYLON.MeshBuilder.CreatePlane("plane", { height: 0.2, width: .5 });
                            miniplame.parent = mesh;
                            miniplame.billboardMode = BABYLON.Mesh.BILLBOARDMODE_ALL
                            miniplame.position.y = 0.7;
                            var advancedTexture = GUI.AdvancedDynamicTexture.CreateForMesh(miniplame, 300, 100);

                            var button1 = GUI.Button.CreateSimpleButton("but1", "Sin datos");
                            button1.width = 1;
                            button1.height = 1;
                            button1.color = "white";
                            button1.fontSize = 50;
                            button1.fontStyle = "bold"
                            button1.background = "#00000066"
                            button1.textBlock.text = data.display_Velocity.toString() + " m/s";

                            var dataToshow = 1;

                            button1.onPointerUpObservable.add(function () {
                                switch (dataToshow) {
                                    case 1: dataToshow = 2
                                        button1.textBlock.text = data.display_Time.toString() + " segs";
                                        break;
                                    case 2: dataToshow = 3
                                        button1.textBlock.text = data.display_Distance.toString() + " mts";
                                        break;
                                    case 3: dataToshow = 1
                                        button1.textBlock.text = data.display_Velocity.toString() + " m/s";
                                        break;

                                    default:
                                        break;
                                }




                            });
                            advancedTexture.addControl(button1);


                            //LOOK TEST WITH QUATERNIONS

                            var look = function (mesh, pos) {
                                var dv = pos.subtract(mesh.position);
                                var yaw = -Math.atan2(dv.z, dv.x) - Math.PI / 2;
                                mesh.rotationQuaternion = BABYLON.Quaternion.RotationYawPitchRoll(yaw, 0, 0);
                            };



                            //scene.registerBeforeRender(function () {
                            //look(miniplame, camera.position);
                            //    })



                        }


                        ghostData(ghost_sphere);
                        ghostLine();


                    }


                    function RefreshDataInMilisecs(time, refreshRate) {

                        var tick = 0;
                        var deltaTime = 0
                        var refreshsteps = Math.round((time / refreshRate))
                        var actualstep = 0;

                        //tick = 1000 milisecs = 1 seg aprox 15 to 20 per frame
                        //timer * 1000 = time to milisecs
                        //aceleration = vel/tiempo alcuadrado
                        // vel(multiplier)
                        //multiplier= CurrentTime/maxTime
                        //tickTimer= tick++
                        var currentTime = 0;

                        var multiplier = function () {

                            var value = currentTime / (time * 1000)

                            return value;
                        }

                        var transformForce = function (mesh, vec) {
                            var mymatrix = new BABYLON.Matrix();
                            mesh.rotationQuaternion.toRotationMatrix(mymatrix);
                            return BABYLON.Vector3.TransformNormal(vec, mymatrix);
                        };

                        var accelObs = scene.onBeforeRenderObservable.add(() => {
                            deltaTime = (scene.deltaTime);

                            currentTime += deltaTime;

                            if (mesh.physicsImpostor) {
                                // mesh.physicsImpostor.setLinearVelocity(impulseDirection.scale(obj_Velocity*multiplier()));
                                mesh.physicsImpostor.setLinearVelocity((transformForce(mesh, impulseDirection.scale(obj_Velocity * multiplier()))));
                            }

                            if (currentTime >= time * 1000) {
                                deltaTime = 0;
                                // if(mesh.physicsImpostor){
                                //     mesh.physicsImpostor.setLinearVelocity(new BABYLON.Vector3(0, 0, 0));
                                //    }
                                scene.onBeforeRenderObservable.remove(accelObs);
                            }
                        })



                        var temporalObs = scene.onBeforeRenderObservable.add(() => {
                            tick += (scene.deltaTime);

                            if (tick >= refreshRate * 1000) {



                                if (actualstep <= refreshsteps) {

                                    //code to refresh here

                                    tick = 0;


                                    display_Time = (obj_Time * (actualstep / refreshsteps)).toFixed(2).toString();

                                    display_Distance = (((obj_Accel * (display_Time * display_Time)) / 2)).toFixed(2).toString();


                                    display_Velocity = (obj_Velocity * (actualstep / refreshsteps)).toFixed(2).toString();

                                    exercise_datadisplay.setViewData
                                        (
                                            "recorrido: " + display_Distance + " metros (m)" + "\n" +
                                            "v: " + display_Velocity + " m/s" + "\n" +
                                            "t: " + display_Time + " segs" + "\n" +
                                            "a: " + obj_Accel + " m/s^2"
                                        )

                                    var info = { display_Distance, display_Time, display_Velocity }

                                    ghosting(mesh, info)

                                    //  if(mesh.physicsImpostor){
                                    //      mesh.physicsImpostor.setLinearVelocity(impulseDirection.scale(display_Velocity));
                                    //  }



                                    //

                                } else {

                                    actualstep = 0;
                                    tick = 0;
                                    if (mesh.physicsImpostor) {
                                        mesh.physicsImpostor.setLinearVelocity(new BABYLON.Vector3(0, 0, 0));
                                    }
                                    scene.onBeforeRenderObservable.remove(temporalObs);
                                }
                                actualstep++
                            }

                        });

                    }

                    RefreshDataInMilisecs(obj_Time, 0.25)



                }



                function CalculateDistance() {
                    //obj_Distance=((obj_Velocity*obj_Time)/2).toFixed(2);
                    obj_Distance = ((obj_Accel * (obj_Time * obj_Time)) / 2).toFixed(2);

                }


                function CalculateFinalVel() {
                    obj_Velocity = (obj_Accel * obj_Time).toFixed(2);

                }


                var CalculateAccel = function (value) {

                    obj_Accel = Math.floor(value * 10) / 10
                    CalculateFinalVel();
                    CalculateDistance();





                }



                var CalculateVelocidad = function (value) {

                    obj_Velocity = Math.floor(value * 10) / 10
                    CalculateDistance();



                }

                var CalculateTime = function (value) {

                    obj_Time = Math.floor(value * 10) / 10
                    CalculateFinalVel();
                    CalculateDistance();

                }


                var displayFValue = function (value) {

                    return Math.floor(value * 10) / 10;

                }


                var controlSlider = new GUI.SliderGroup("Control de variables", "S");

                controlSlider.addSlider("Aceleracion", CalculateAccel, "m/s^2", 1, 10, 1, displayFValue);
                controlSlider.addSlider("Tiempo", CalculateTime, "segs", 1, 5, 1, displayFValue);

                exercise_control.controlPanel.addGroup(controlSlider);
                exercise_control.controlPanel.heightInPixels = 150;


                //CALLS TO DISPLAYS AND OTHER GUI COMPONENTS

                var execute_obs = exercise_control.button.onPointerUpObservable.add(function () {
                    activate();

                });

                exercise_control.addObserver(execute_obs);




            }

