
import * as BABYLON from 'babylonjs';

export function createPlayGround(scene) {

    var ground = BABYLON.MeshBuilder.CreateGround("ground", { height: 10, width: 10, subdivisions: 4 }, scene);
    ground.visibility = 0.1;
    //ground.physicsImpostor = new BABYLON.PhysicsImpostor(ground, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });
    //ground.XRPickable =true;
    var ground2 = BABYLON.MeshBuilder.CreateBox("ground2", { height: -0.33, width: 10, depth: 10 }, scene);
    ground2.position.set(0, 0, 0)
    ground2.visibility = 1;
    ground2.physicsImpostor = new BABYLON.PhysicsImpostor(ground2, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });


    // Add and manipulate meshes in the scene
    var wallF = BABYLON.MeshBuilder.CreateBox("frontwall", { height: 5, width: 10, depth: 0.33 }, scene);
    wallF.position.set(0, 2.5, 5)
    wallF.visibility = 0.1;
    wallF.physicsImpostor = new BABYLON.PhysicsImpostor(wallF, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });

    // Add and manipulate meshes in the scene
    var wallB = BABYLON.MeshBuilder.CreateBox("backwall", { height: 5, width: 10, depth: 0.33 }, scene);
    wallB.position.set(0, 2.5, -5)
    wallB.visibility = 0.1;
    wallB.physicsImpostor = new BABYLON.PhysicsImpostor(wallB, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });

    // Add and manipulate meshes in the scene
    var wallR = BABYLON.MeshBuilder.CreateBox("rightwall", { height: 5, width: 0.33, depth: 10 }, scene);
    wallR.position.set(5, 2.5, 0)
    wallR.visibility = 0.1;
    wallR.physicsImpostor = new BABYLON.PhysicsImpostor(wallR, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });

    // Add and manipulate meshes in the scene
    var wallL = BABYLON.MeshBuilder.CreateBox("leftwall", { height: 5, width: 0.33, depth: 10 }, scene);
    wallL.position.set(-5, 2.5, 0)
    wallL.visibility = 0.1;
    wallL.physicsImpostor = new BABYLON.PhysicsImpostor(wallL, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });

    ground.addChild(ground2);
    ground.addChild(wallF);
    ground.addChild(wallB);
    ground.addChild(wallR);
    ground.addChild(wallL);

    return ground2;

}

