export function createPlayGroundGigant(scene) {


    var groundMaterial = new BABYLON.StandardMaterial("ground1", scene);
    groundMaterial.diffuseTexture = new BABYLON.Texture("textures/grass.jpg", scene);
    groundMaterial.diffuseTexture.uScale = 15;
    groundMaterial.diffuseTexture.vScale = 15;
    groundMaterial.specularColor = new BABYLON.Color3(0, 0, 0);

    //    var ground = BABYLON.MeshBuilder.CreateGround("ground", {height: 100, width: 100, subdivisions: 4}, scene);
    //   ground.position.set(0,0,0)   
    //ground.visibility=0.5;
    //ground.position.y =+0.33;
    //    ground.material = groundMaterial;
    //ground.physicsImpostor = new BABYLON.PhysicsImpostor(ground, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });
    //ground.XRPickable =true;


    var ground2 = BABYLON.MeshBuilder.CreateBox("ground2", { height: 3, width: 100, depth: 100 }, scene);
    ground2.position.set(0, -1.5, 0)
    ground2.visibility = 1;
    ground2.physicsImpostor = new BABYLON.PhysicsImpostor(ground2, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });
    ground2.material = groundMaterial;

    // Add and manipulate meshes in the scene
    var wallF = BABYLON.MeshBuilder.CreateBox("frontwall", { height: 50, width: 100, depth: 0.33 }, scene);
    wallF.position.set(0, 25, 50)
    wallF.visibility = 0.1;
    wallF.physicsImpostor = new BABYLON.PhysicsImpostor(wallF, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });
    wallF.checkCollisions = true;

    // Add and manipulate meshes in the scene
    var wallB = BABYLON.MeshBuilder.CreateBox("backwall", { height: 50, width: 100, depth: 0.33 }, scene);
    wallB.position.set(0, 25, -50)
    wallB.visibility = 0.1;
    wallB.physicsImpostor = new BABYLON.PhysicsImpostor(wallB, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });
    wallB.checkCollisions = true;

    // Add and manipulate meshes in the scene
    var wallR = BABYLON.MeshBuilder.CreateBox("rightwall", { height: 50, width: 0.33, depth: 100 }, scene);
    wallR.position.set(50, 25, 0)
    wallR.visibility = 0.1;
    wallR.physicsImpostor = new BABYLON.PhysicsImpostor(wallR, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });
    wallR.checkCollisions = true;

    // Add and manipulate meshes in the scene
    var wallL = BABYLON.MeshBuilder.CreateBox("leftwall", { height: 50, width: 0.33, depth: 100 }, scene);
    wallL.position.set(-50, 25, 0)
    wallL.visibility = 0.1;
    wallL.physicsImpostor = new BABYLON.PhysicsImpostor(wallL, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 0, restitution: 0.5 });
    wallL.checkCollisions = true;

    //ground.addChild(ground2);
    ground2.addChild(wallF);
    ground2.addChild(wallB);
    ground2.addChild(wallR);
    ground2.addChild(wallL);

    return ground2;

}
