import React, { Component, useRef } from 'react'
import Main from '../../WebLayout/Main';
import * as BABYLON from 'babylonjs';
import BabylonScene from '../../BabylonScene/'; // import the component above linking to file we just created.
import * as GUI from 'babylonjs-gui';
import 'babylonjs-loaders';


import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'

import { makeStyles } from '@material-ui/core/styles';

import { Container, Button } from '@material-ui/core';

import { useAuth } from '../../Contexts/AuthContext'

import * as Ground from '../../BabylonScene/components/Ground'

import * as GizmoManager from '../../BabylonScene/components/XRGizmoManager'

import XRQuizDisplay from '../../FisicARtivo_Components/XRQuizDisplay';
import XRDinamicsDataDisplay from '../../FisicARtivo_Components/XRDinamicsDataDisplay';
import XRControlDisplay from '../../FisicARtivo_Components/XRControlDisplay';

import XRDisplayPlane from '../../FisicARtivo_Components/XRDisplayPlane';

import { ImageInfoCard } from '../../FisicARtivo_Components/InfoCards';


const useStyles = makeStyles(theme => ({
    imagebox: {
        //backgroundImage: 'url(' + drawerImage + ')',   
        height: '100%',
        width: '60%',
        display: 'flex',
        flexDirection: 'column',
    },
    card: {
        height: '100%',

        flexDirection: 'column',
    },
    media: {
        height: 400,

    },
    babylonbuttons: {
        '& > *': {
            margin: theme.spacing(1)
        },

    },
}));



function GetMainContent() {

    const classes = useStyles();


    var onSceneMount = (e: SceneEventArgs) => {
        const { canvas, scene, engine } = e;

        engine.displayLoadingUI();
        // This creates and positions a free camera (non-mesh)
        // const camera = new BABYLON.FreeCamera("camera1", new BABYLON.Vector3(0, 5, -10), scene);
        const camera_initialPos = new BABYLON.Vector3(-4, .82, 0);
        const camera_initialTarget = new BABYLON.Vector3(0, 1, 0);
        const camera_speed = 0.7;
        const camera_angularSensibility = 1200;

        var camera = new BABYLON.UniversalCamera("camera1", camera_initialPos, scene);
        // This targets the camera to scene origin
        camera.setTarget(camera_initialTarget);
        // This attaches the camera to the canvas
        //camera.attachControl(canvas, false);

        camera.speed = camera_speed;
        camera.angularSensibility = camera_angularSensibility;
        camera.inertia = .5;


        // This creates a light, aiming 0,1,0 - to the sky (non-mesh)
        // const light = new BABYLON.HemisphericLight("light1", new BABYLON.Vector3(0, 1, 0), scene);
        // Default intensity is 1. Let's dim the light a small amount
        // light.intensity = 0.7;


        // Light
        const light = new BABYLON.PointLight("point", new BABYLON.Vector3(20, 20, 10), scene);
        light.intensity = 0.9;

        // Skybox
        var skybox = BABYLON.MeshBuilder.CreateBox("SkyBox", { size: 1000.0 }, scene);
        var skyboxMaterial = new BABYLON.StandardMaterial("skyBox", scene);
        skyboxMaterial.backFaceCulling = false;
        skyboxMaterial.reflectionTexture = new BABYLON.HDRCubeTexture("textures/lilienstein_4k.hdr", scene, 512);
        skyboxMaterial.reflectionTexture.coordinatesMode = BABYLON.Texture.SKYBOX_MODE;
        skybox.material = skyboxMaterial;


        // physics
        scene.enablePhysics(new BABYLON.Vector3(0, -9.81, 0), new BABYLON.AmmoJSPlugin());
        //scene.enablePhysics(new BABYLON.Vector3(0, -9.81, 0), new BABYLON.OimoJSPlugin()) 
        //scene.enablePhysics(new BABYLON.Vector3(0, -9.81, 0), new BABYLON.CannonJSPlugin());

        //var physicsEngine = scene.getPhysicsEngine();
        //physicsEngine.setSubTimeStep(1);


        // Enable Collisions
        scene.collisionsEnabled = true;

        //Then apply collisions and gravity to the active camera
        camera.checkCollisions = true;
        //camera.applyGravity = true;

        //Set the ellipsoid around the camera (e.g. your player's size)
        camera.ellipsoid = new BABYLON.Vector3(.5, .8, .5);

        var ground = Ground.createPlayGroundGigant(scene);


        //var ground= Ground.createPlayGroundLarge(scene);

        //finally, say which mesh will be collisionable
        ground.checkCollisions = true;


        async function createScene() {


            // AR availability check and GUI in non-AR mode
            let arAvailable = await BABYLON.WebXRSessionManager.IsSessionSupportedAsync('immersive-ar')

            //ACTIVACION DE MODO XR (REVISAR SOPORTE, BOTON MODO XR, ETC)

            var xr = await scene.createDefaultXRExperienceAsync({

                disableDefaultUI: true,
                //floorMeshes: [ground],
                uiOptions: {

                    sessionMode: "immersive-ar"
                    //, referenceSpaceType: ar_supported ? 'unbounded' : 'local-floor'
                    , referenceSpaceType: "local-floor"
                },
                inputOptions: { doNotLoadControllerMeshes: true },
                //optionalFeatures: true
            });



            /*   xr.baseExperience.onInitialXRPoseSetObservable.add((xrCamera) => {
                    // floor is at y === 2
                    camera.y = 2;
                
                });*/

            // Get the Feature Manager and from it the HitTesting fearture 
            //const fm = xr.baseExperience.featuresManager;

            // Get XR Camera Created by Basic Experience Helper 
            //const xrCamera = xr.baseExperience.camera




            var gizmoManager = GizmoManager.addXRGizmoManager(BABYLON, scene);
            gizmoManager.selectMode(0);


            var pickedMesh = null;
            var attachedMesh = null;
            var pressed = false;

            //observables generales globales para picking meshes


            scene.onPointerObservable.add((pointerInfo) => {
                pickedMesh = pointerInfo.pickInfo.pickedMesh;

                //if (pointerInfo.type === 4) {
                // if (pointerInfo.pickInfo.pickedMesh.XRPickable) {
                //      pointerInfo.pickInfo.pickedMesh.material = pointerInfo.pickInfo.pickedMesh.material || new BABYLON.StandardMaterial("mat", scene);
                //      pointerInfo.pickInfo.pickedMesh.material.diffuseColor = BABYLON.Color3.Random();
                // }
                //}

                if (pointerInfo.type === BABYLON.PointerEventTypes.POINTERDOWN) {

                    if (pointerInfo.pickInfo.pickedMesh && pointerInfo.pickInfo.pickedMesh.XRPickable) {

                         scene.constantlyUpdateMeshUnderPointer = true;

                        pressed = true;

                        attachedMesh = pickedMesh;

                        gizmoManager.attachToMesh(attachedMesh);

                    }

                    if (pointerInfo.pickInfo.pickedMesh && pointerInfo.pickInfo.pickedMesh.nonXREditable) {

                    }

                    if (pointerInfo.pickInfo.pickedMesh && !pointerInfo.pickInfo.pickedMesh.XRPickable && !pointerInfo.pickInfo.pickedMesh.nonXREditable) {

                        attachedMesh = null;
                        gizmoManager.attachToMesh(null);
                    }

                }

                if (pointerInfo.type === BABYLON.PointerEventTypes.POINTERUP) {

                    scene.constantlyUpdateMeshUnderPointer = false;

                    if (pointerInfo.pickInfo.pickedMesh && pointerInfo.pickInfo.pickedMesh.XRPickable && pickedMesh.physicsImpostor) {


                        // pickedMesh.physicsImpostor.wakeUp();
                        pressed = false;
                        //pickedMesh.physicsImpostor.dispose();
                        //pickedMesh.physicsImpostor = new BABYLON.PhysicsImpostor(pickedMesh, pickedMesh.physicsImpostor.type, { mass: pickedMesh.physicsImpostor.mass, restitution: pickedMesh.physicsImpostor.restitution }, scene);

                        pickedMesh = null;

                    }

                }

            });

            scene.onBeforePhysicsObservable.add(() => {

                if (pressed && pickedMesh && pickedMesh.XRPickable && pickedMesh.physicsImpostor) {

                    pickedMesh.physicsImpostor._physicsBody.setLinearVelocity(0, 0, 0);
                    pickedMesh.physicsImpostor._physicsBody.setAngularVelocity(0, 0, 0);
                }


            });



            const normalGUI = GUI.AdvancedDynamicTexture.CreateFullscreenUI("fullscreenGUI");
            //normalGUI.scaleTo();

            var intro_info = new GUI.Rectangle("intro_info")
            intro_info.background = "black"
            intro_info.color = "blue"
            intro_info.width = "80%";
            intro_info.height = "50%";

            const text_bad = new GUI.TextBlock("text_bad");
            text_bad.fontFamily = "Helvetica";
            text_bad.textWrapping = true;
            text_bad.text = "modo de realidad aumentada (modo XR) no esta disponible para este dispositivo, por favor, asegurate de usar Chrome como navegador web y un dispositivo Android";
            text_bad.color = "white";
            text_bad.fontSize = "5%";
            text_bad.height = "400px"
            text_bad.paddingLeft = "10px"
            text_bad.paddingRight = "10px"

            const text_good = new GUI.TextBlock("text_good");
            text_good.fontFamily = "Helvetica";
            text_good.textWrapping = true;
            text_good.text = 'Compatibilidad con realidad aumentada detectada: porfavor, ingresa al modo de realidad aumentada con el boton "ENTRAR A MODO XR" en la parte inferior de esta pagina. Al activar el modo RA, aparecer un mensaje de exito';
            text_good.color = "white";
            text_good.fontSize = "5%";
            text_good.height = "400px"
            text_good.paddingLeft = "10px"
            text_good.paddingRight = "10px"

            var noXRPanel = new GUI.StackPanel();
            intro_info.addControl(noXRPanel);

            normalGUI.addControl(intro_info)


            if (!arAvailable) {
                noXRPanel.addControl(text_bad);
            } else {
                noXRPanel.addControl(text_good);
            }


            //OBJECTS INSIDE XR MODE (RENDER WHEN THE XR MODE IS ACTIVATED)


            var xr_info = new GUI.Rectangle("intro_info")
            xr_info.background = "black"
            xr_info.color = "blue"
            xr_info.width = "100%";
            xr_info.height = "100%";

            const text1 = new GUI.TextBlock("text1");
            text1.fontFamily = "Helvetica";
            text1.textWrapping = true;
            text1.text = "Si puedes ver esto, significa que tu dispositivo es compatible con AR de FisicARtivo!";
            text1.color = "white";
            text1.fontSize = "36px";
            text1.height = "400px"
            text1.paddingLeft = "10px"
            text1.paddingRight = "10px"

            var XRPanel = new GUI.StackPanel();
            xr_info.addControl(XRPanel);
            XRPanel.addControl(text1);


            var displayPlane = new BABYLON.MeshBuilder.CreatePlane("displayPlaneinfo", {
                width: 1.6,
                height: .9
            }, scene);

            displayPlane.nonXREditable = true;
            displayPlane.XRPickable = true;

            //displayPlane.billboardMode = BABYLON.Mesh.BILLBOARDMODE_ALL       

            var container_texture = new GUI.AdvancedDynamicTexture.CreateForMesh(displayPlane);
            container_texture.scaleTo(800, 450)


            var local_pos = new BABYLON.Vector3(1, 1.5, 0);
            displayPlane.position = local_pos; //position sphere relative to world
            displayPlane.rotation.y = (90 * (Math.PI / 180))


            var container = new GUI.Rectangle("container")
            //container.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_CENTER;
            //container.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_TOP;
            container.width = 1;
            container.height = 1;
            //container.cornerRadius = 20;
            //container.color = "Orange";
            container.thickness = 0;
            container.background = "white";
            container.alpha = 0.05;
            //container.paddingTopInPixels = 15;
            //container.paddingLeftInPixels = 15;
            container.zIndex = -1;


            container_texture.addControl(container);
            container_texture.addControl(xr_info);


            var materialWood = new BABYLON.StandardMaterial("wood", scene);
            materialWood.diffuseTexture = new BABYLON.Texture("../textures/crate.png", scene);
            materialWood.emissiveColor = new BABYLON.Color3(0.5, 0.5, 0.5);

            var box = BABYLON.Mesh.CreateBox("wood_box", 1, scene);
            box.scaling = new BABYLON.Vector3(0.4, 0.4, 0.4)
            //box.position.set(-1.5,.33,0);
            box.position = new BABYLON.Vector3(1, 0, 0);
            box.material = materialWood;
            box.physicsImpostor = new BABYLON.PhysicsImpostor(box, BABYLON.PhysicsImpostor.BoxImpostor, { mass: 5 }, scene);
            box.XRPickable = true;

            box.isVisible = false;
            displayPlane.isVisible = false;



            //Hide GUI in AR mode
            xr.baseExperience.sessionManager.onXRSessionInit.add(() => {
                intro_info.isVisible = false;
                box.isVisible = true;
                displayPlane.isVisible = true;

                ground.visibility = 0;
                skybox.isVisible = false;



            })
            xr.baseExperience.sessionManager.onXRSessionEnded.add(() => {
                intro_info.isVisible = true;
                box.isVisible = false;
                displayPlane.isVisible = false;

                ground.visibility = 1;
                skybox.isVisible = true;
            })


            var loader = new BABYLON.AssetsManager(scene);

            loader.onFinish = function () {

                engine.runRenderLoop(() => {
                    if (scene) {
                        window.document.title = engine.getFps().toFixed() + " fps";
                        scene.render();

                    }
                });
            };

            scene.executeWhenReady(function () {

                engine.hideLoadingUI();

                if (document.getElementById("xr-button")) {
                    let XRbutton = document.getElementById("xr-button");

                    XRbutton.addEventListener("click", async function () {

                        if (xr.baseExperience.state === BABYLON.WebXRState.NOT_IN_XR) {

                            xr.input.xrCamera.position = new BABYLON.Vector3(-3, 1, 0)
                            xr.input.xrCamera.setTarget(BABYLON.Vector3.Zero());
                            await xr.baseExperience.enterXRAsync("immersive-ar", "local-floor")

                        } else if (xr.baseExperience.state === BABYLON.WebXRState.IN_XR) {
                            await xr.baseExperience.exitXRAsync()
                        }
                    });

                }


                if (document.getElementById("fullscreen-button")) {
                    let button = document.getElementById("fullscreen-button")

                    button.addEventListener("click", async function () {
                        //engine.enterFullScreen(true);

                        engine.switchFullscreen(false);
                    })
                }

            });


            loader.load();

        }

        createScene();

    }


    return (
        <React.Fragment>
            <Container maxWidth='xl'>

                <Typography align='center' variant="h6">Gira esta pantalla en horizontal (panoramico) para alargar contenido </Typography>

                <Container align='center' maxWidth='sm' className={classes.babylonbuttons}>
                    <Button id='xr-button' size='medium' variant="contained" color="primary">
                        <Typography variant="h6">Entrar a modo XR</Typography>
                    </Button>
                    <Button id='fullscreen-button' m={0.5} size='medium' variant="contained" color="secondary">
                        <Typography variant="h6">pantalla completa</Typography>
                    </Button>
                </Container>



                <Container align='center'>

                    <Box width="85%" p={1} my={0.5}>
                        <BabylonScene onSceneMount={onSceneMount} />
                    </Box>
                </Container>




            </Container>
        </React.Fragment>
    )
}



export default GetMainContent