
import { firestore } from '../firebase'
import { Timer } from "easytimer.js";

export const globalTimer = new Timer();

export const exerciseTimer = new Timer();

var currentUser = {};

var exerciseData = {};

var LoadedUserData = {};


export function setCurrentUser(user) {
    currentUser = user;
}

export function setExerciseData(exercise) {
    exerciseData = exercise;
}

export function getExerciseData() {
    return exerciseData;
}


export function checkUserExerciseData(exercise_id) {

    if (currentUser) {
        firestore.collection('users').doc(currentUser.uid).collection('exercises').doc(exercise_id).get().then(response => {

         
            if (response.exists) {

                LoadedUserData = response.data();

                //exerciseData=LoadedUserData
                setExerciseData(LoadedUserData);
                
                

            }


        }, (error => {
            console.log(error.message)
        }))
    }

}

export function updateUserExerciseData(exercise_id) {
    isExerciseComplete();
    addTotalTime();
    firestore.collection('users').doc(currentUser.uid).collection('exercises').doc(exercise_id).set(exerciseData);

}

export function getQuizByID(id) {

    for (let [key] of Object.entries(exerciseData.quizes)) {

        if (exerciseData.quizes[key].id == id) {

            return exerciseData.quizes[key];
        }

    }

}


export function addQuiz(loaded_exercise_index) {
    var quiz = {
        id: 'quiz_' + loaded_exercise_index,
        time: '00:00',
        errors: 0,
        isCompleted: false
    }
    var test = Object.keys(exerciseData.quizes)
    //Object.defineProperty()
    //exerciseData.quizes
    exerciseData.quizes.push(quiz);

}

export function addQuizTimebyID(id, time) {

    var isCompleted = getQuizByID(id).isCompleted;
    if (!isCompleted) {
        getQuizByID(id).time = time;
    }

}

export function addTotalTime() {

    if (!exerciseData.isCompleted) {
        exerciseData.generalTime = globalTimer.getTimeValues().toString();

    } else {
        globalTimer.stop();
    }

}

export function addQuizErrorByID(id) {

    var isCompleted = getQuizByID(id).isCompleted;
    if (!isCompleted) {
        getQuizByID(id).errors += 1;
    }

}

export function addTotalError(id) {

    var isCompleted = getQuizByID(id).isCompleted;
    if (!isCompleted) {
        exerciseData.totalErrors += 1

    }

}

export function isExerciseComplete() {

    var checkCompletedQuizes = 0
    var quizlist = Object.entries(exerciseData.quizes)

    for (let [key] of quizlist) {

        if (exerciseData.quizes[key].isCompleted == true) {
            checkCompletedQuizes++

        }

    }
    if (checkCompletedQuizes == quizlist.length) {
        exerciseData.isCompleted = true;
    }
    else {
        exerciseData.isCompleted = false;
    }



}




export function getEmotionData(emotiondata, ejercicio) {


    if (currentUser && emotiondata != undefined) {

        var tempData = JSON.parse(JSON.stringify(emotiondata));

        var dateObj = new Date();
        var month = dateObj.getUTCMonth() + 1; //months from 1-12
        var day = dateObj.getUTCDate();
        var year = dateObj.getUTCFullYear();
        var hours = dateObj.getUTCHours();
        var minutes = dateObj.getUTCMinutes();

        var tag = ("quiz_" + ejercicio + "-" + year + "-" + month + "-" + day + " " + hours + ":" + minutes).toString();
        firestore.collection('users').doc(currentUser.uid).collection('emotion_capture').doc(tag).set(tempData);
    }


}


