            async function quiz1_models() {

                function rotatewithgrades(mesh, grades) {

                    mesh.rotationQuaternion = new BABYLON.Quaternion.RotationAxis(new BABYLON.Vector3(0, 1, 0), (grades * (Math.PI / 180)));
                }


                function attachToCollider(modelMeshes, rootMesh, collider, BABYLON_Impostor_Type) {

                    var boundingMinMax = modelMeshes[rootMesh].getHierarchyBoundingVectors();
                    var bounds = boundingMinMax.max.subtract(boundingMinMax.min);
                    var geometricCenter = boundingMinMax.max.add(boundingMinMax.min).scale(0.5);

                    collider.scaling.copyFrom(bounds);
                    collider.XRPickable = true;
                    collider.visibility = 0;

                    collider.physicsImpostor = new BABYLON.PhysicsImpostor(collider, BABYLON_Impostor_Type, { mass: 3 }, scene);

                    collider.position = geometricCenter;

                    modelMeshes[rootMesh].setParent(collider);




                }

                var defaultmeshes = [];


                var cannonmodel = (await BABYLON.SceneLoader.ImportMeshAsync("", "../3DModels/", "cannonball.gltf", scene));

                var cannonmesh = cannonmodel.meshes;

                var materialWood = new BABYLON.StandardMaterial("wood", scene);
                materialWood.diffuseTexture = new BABYLON.Texture("../textures/cannon_tex.jpg", scene);
                materialWood.emissiveColor = new BABYLON.Color4.FromHexString("#8C7853")
                materialWood.diffuseTexture.uScale = 20.0;//Repeat 5 times on the Vertical Axes
                materialWood.diffuseTexture.vScale = 20.0;//Repeat 5 times on the Horizontal Axes

                cannonmesh[1].material = materialWood;


                cannonmesh[0].scaling.scaleInPlace(0.15);
                cannonmesh[0].position.set(-.5, .33, 0);




                var cannoncolider = BABYLON.MeshBuilder.CreateBox("cannon", { height: cannonmesh[1].height, width: cannonmesh[1].width, depth: cannonmesh[1].depth }, scene);

                attachToCollider(cannonmesh, 0, cannoncolider, BABYLON.PhysicsImpostor.BoxImpostor);

                //GIRA CON CUARTERNION NO CON .ROTACION (FUNCIONES EULER)

                rotatewithgrades(cannoncolider, 90);

                //buscollider.rotationQuaternion = new BABYLON.Quaternion.RotationAxis(new BABYLON.Vector3(0, 1, 0), (90*(Math.PI/180)) );                


                defaultmeshes.push(cannonmesh);
                defaultmeshes.push(cannoncolider);

                section_infodisplay.logicManager.AddDisposableStuff(cannonmesh)
                section_infodisplay.logicManager.AddDisposableStuff(cannoncolider)


                return defaultmeshes;

            }

