            function manageStuff() {



                function addExercise(exercise) {
                    exercise_list.push(exercise)
                    quizesList.push(exercise.quiz);
                }

                function LoadQuiz(number) {
                    exercise_quizdisplay.loadquiz(quizesList[number])
                }

                function Start(number) {
                    loaded_exercise_index = number;
                    exercise_list[number].Load_Models().then(function (meshes) {
                        ClearStuff();
                        loaded_meshes = meshes;

                        if (exercise_list[number].Load_Logic) {

                            exercise_list[number].Load_Logic();
                            LoadQuiz(number);
                            QuizTimer.reset();
                        }




                    });
                }

                function Reset() {

                    Start(loaded_exercise_index);

                }

                exercise_control.button2.onPointerUpObservable.add(function () {
                    Reset();
                });

                function ClearStuff() {

                    exercise_control.clearObservers();
                    exercise_control.clearItems();
                    exercise_datadisplay.clearViewData();


                    console.log("reseteando");
                    if (loaded_onBeforeRenderObservables != undefined) {

                        loaded_onBeforeRenderObservables.forEach(value => {

                            scene.onBeforeRenderObservable.remove(value);
                        });

                    }
                    if (loaded_meshes != undefined) {

                        loaded_meshes.forEach(value => {

                            if (Array.isArray(value)) {
                                value.forEach(value => value.dispose());
                            } else { value.dispose(); }
                        });

                        // addSceneModels();

                    }

                }

                var manager = {
                    addExercise,
                    LoadQuiz,
                    Start,
                    Reset,
                    ClearStuff,
                }

                return manager;

            }



