import * as BABYLON from 'babylonjs'
import * as GUI from 'babylonjs-gui';


export default function InfoCardsManager(scene, size) {


    var displayPlane = new BABYLON.MeshBuilder.CreatePlane("displayPlaneinfo", {
        width: size * (1.9),
        height: size * (0.9)
    }, scene);

    displayPlane.nonXREditable = true;
    displayPlane.XRPickable = true;
    //displayPlane.billboardMode = BABYLON.Mesh.BILLBOARDMODE_ALL       


    //var matrix = mesh.computeWorldMatrix(true);  // force calculation of world matrix
    var local_pos = new BABYLON.Vector3(1, 1, 0);
    //var local_pos = new BABYLON.Vector3(-0.1, 1.2, 0); //top middle of box relative to box
    //var global_pos = BABYLON.Vector3.TransformCoordinates(local_pos, matrix); //calculate world position
    displayPlane.position = local_pos; //position sphere relative to world
    displayPlane.rotation.y = (90 * (Math.PI / 180))

    var container = new GUI.AdvancedDynamicTexture.CreateForMesh(displayPlane);
    container.scaleTo(1600, 900)

    //container.useSmallestIdeal = true
    //container.idealWidth = 1600;
    //container.idealHeight = 900;


    var mainstructure_grid = new GUI.Grid("mainStructure");
    mainstructure_grid.addColumnDefinition(0.1);
    mainstructure_grid.addColumnDefinition(0.8);
    mainstructure_grid.addColumnDefinition(0.1);
    mainstructure_grid.addRowDefinition(1);

    container.addControl(mainstructure_grid);

    var cards_container = [];
    var cards_container_index = 0


    function addCard(card) {

        cards_container.push(card);

    }

    function Start(cardnumber) {

        if (cards_container.length > 0) {

            cards_container_index = cardnumber;
            mainstructure_grid.addControl(cards_container[cardnumber].info_image_container, 0, 1);

            var onload = getCurrentCard();
            logicManager.LoadStuff(onload);
            logicManager.Start();

        }
    }

    function getCurrentCard() {

        return cards_container[cards_container_index];
    }

    function isLimitList() {
        console.log(cards_container_index)
        console.log(cards_container.length)

        var limitcheck = cards_container_index + 1
        var sublimitcheck = cards_container_index - 1

        if (limitcheck > cards_container.length) {

            return true;

        }
        else if (sublimitcheck < 0) {

            return true;

        } else {

            return false;

        }

    }

    function prevImage() {

        if (cards_container.length > 0) {

            var tem_index = cards_container_index;
            var prev_card = cards_container[cards_container_index]
            cards_container_index--

            if (cards_container_index <= 0) {

                cards_container_index = 0
            }

            if (tem_index != cards_container_index) {

                var card = cards_container[cards_container_index]

                //info_image_container.source = card.src;
                mainstructure_grid.removeControl(prev_card.info_image_container)
                mainstructure_grid.addControl(card.info_image_container, 0, 1)


                var onload = getCurrentCard();
                logicManager.LoadStuff(onload);
                logicManager.Start();
            }
        }




    }

    function nextImage() {

        if (cards_container.length > 0) {

            var tem_index = cards_container_index;
            var prev_card = cards_container[cards_container_index]
            cards_container_index++

            if (cards_container_index >= cards_container.length) {
                cards_container_index = cards_container.length - 1
            }


            if (tem_index != cards_container_index) {
                var card = cards_container[cards_container_index]
                //info_image_container.source = card.src;
                mainstructure_grid.removeControl(prev_card.info_image_container)
                mainstructure_grid.addControl(card.info_image_container, 0, 1)

                var onload = getCurrentCard();
                logicManager.LoadStuff(onload);
                logicManager.Start();
            }

        }

    }



    //addGrid.addControl(image1.info_image_container, 0, 1);

    var button_previous = GUI.Button.CreateImageOnlyButton("but_prev", "images/left.png");
    //button.width = 0.5;
    button_previous.height = .8;
    button_previous.color = "white";
    button_previous.fontSize = 20;
    button_previous.background = "#1388AF";
    button_previous.image.paddingBottomInPixels = 100
    button_previous.image.paddingTopInPixels = 100
    button_previous.image.paddingLeftInPixels = 10
    button_previous.image.paddingRightInPixels = 10
    //button.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
    //button.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_CENTER;               
    //button.top = "-10px";

    //button.onPointerClickObservable.add(ExecuteButton);
    //panel.addControl(button_previous);
    mainstructure_grid.addControl(button_previous, 0, 0);

    button_previous.onPointerUpObservable.add(() => {
        prevImage();
    })



    var button_next = GUI.Button.CreateImageOnlyButton("but_pos", "images/right.png");
    //button.width = 0.5;
    button_next.height = .8;
    button_next.color = "white";
    button_next.fontSize = 20;
    button_next.background = "#1388AF";
    button_next.image.paddingBottomInPixels = 100
    button_next.image.paddingTopInPixels = 100
    button_next.image.paddingLeftInPixels = 10
    button_next.image.paddingRightInPixels = 10
    //button.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
    //button.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_CENTER;               
    //button.top = "-10px";

    //button.onPointerClickObservable.add(ExecuteButton);
    //panel.addControl(button_next);
    mainstructure_grid.addControl(button_next, 0, 2);

    button_next.onPointerUpObservable.add(() => {
        nextImage();
    })


    function open() {


        var openKeys = [];
        openKeys.push({
            frame: 0,
            value: 0 //BABYLON.Vector2.Zero()
        });
        openKeys.push({
            frame: 10,
            value: 1 // new BABYLON.Vector2(1, 1)
        });

        var animationOpen = new BABYLON.Animation(
            'scaleAnimationOpen',
            'scaleX',
            30,
            BABYLON.Animation.ANIMATIONTYPE_FLOAT, //VECTOR2,
            BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT
        );
        animationOpen.setKeys(openKeys);

        let easingFunction = new BABYLON.BounceEase(1, 4);
        easingFunction.setEasingMode(BABYLON.EasingFunction.EASINGMODE_EASEOUT);
        animationOpen.setEasingFunction(easingFunction);

        //container.animations.push(animationOpen);

        //scene.beginAnimation(container, 0, 100, false, 1);
        //scene.beginDirectAnimation(container, [animationOpen], 0, openKeys[openKeys.length-1].frame, false, 1);
        scene.beginDirectAnimation(container, [animationOpen], 0, 100, false, 1);

    }

    function close() {


        var keys = [];
        keys.push({
            frame: 0,
            value: 1
        });
        keys.push({
            frame: 10,
            value: 0
        });


        var animationClose = new BABYLON.Animation(
            'scaleAnimation',
            'scaleX',
            10,
            BABYLON.Animation.ANIMATIONTYPE_FLOAT,
            BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT
        );
        animationClose.setKeys(keys);

        let easingFunction = new BABYLON.ExponentialEase(9.7); // BABYLON.QuarticEase()
        easingFunction.setEasingMode(BABYLON.EasingFunction.EASINGMODE_EASEINOUT);
        animationClose.setEasingFunction(easingFunction);

        //container.animations.push(animationClose);

        //scene.beginAnimation(container, 0, 100, false, 1, () => {
        //menuPlane.dispose();
        //});

        scene.beginDirectAnimation(container, [animationClose], 0, 100, false, 1);

    }

    var observables_list = [];

    function addObserver(params) {

        observables_list.push(params);

    }

    function removeObserver(number) {
        // button.onPointerUpObservable.remove(observables_list[number])
        // observables_list.splice(number,1);
    }

    function clearObservers() {
        //  button.onPointerUpObservable.clear();
        //  observables_list=[]
    }

    function clearItems() {



    }


    function LogicManager(scene) {

        var temp_loable_element = {}
        var temp_disposable_stuff = []
        var temp_observables = []
        var temp_GUI = []

        function LoadStuff(element) {

            temp_loable_element = element;
        }

        function AddDisposableStuff(element) {
            temp_disposable_stuff.push(element)
        }

        function AddObservables(observable) {
            temp_observables.push(observable)
        }

        function AddTemporalGUI(item) {
            temp_GUI.push(item)
        }


        function Start() {

            if (temp_loable_element.Load_Models) {

                temp_loable_element.Load_Models().then(function (meshes) {
                   
                    ClearAll();
                    temp_disposable_stuff = meshes;

                    if (temp_loable_element.Load_Logic) {

                        temp_loable_element.Load_Logic();

                    }

                });

            } else if (temp_loable_element.Load_Logic) {

                temp_loable_element.Load_Logic();

            }



        }

        function ClearAll() {


            if (temp_observables.length > 0) {
               
                temp_observables.forEach(value => {

                    scene.onBeforeRenderObservable.remove(value);
                });

                temp_observables = [];

            }

            if (temp_disposable_stuff.length > 0) {
               
                temp_disposable_stuff.forEach(value => {

                    if (Array.isArray(value)) {
                        value.forEach(value => { if (value.dispose) { value.dispose() } });
                    } else { if (value.dispose) { value.dispose() } }
                });

                temp_disposable_stuff = [];


            }

            if (temp_GUI.length > 0) {
                
                temp_GUI.forEach(value => {

                    if (Array.isArray(value)) {
                        value.forEach(value => { if (value.dispose) { value.dispose() } });
                    } else { if (value.dispose) { value.dispose() } }
                });

                temp_GUI = [];

            }

        }

        function Reset() {

            Start();

        }

        function ReloadModels() {

            if (temp_disposable_stuff.length > 0) {

                temp_disposable_stuff.forEach(value => {

                    if (Array.isArray(value)) {
                        value.forEach(value => { if (value.dispose) { value.dispose() } });
                    } else { if (value.dispose) { value.dispose() } }
                });
                temp_disposable_stuff = [];

            }

            if (temp_loable_element.Load_Models) {

                temp_loable_element.Load_Models().then(function (meshes) {

                    temp_disposable_stuff = meshes;


                });

            }

        }

        function ReloadLogic() {


            if (temp_observables.length > 0) {

                temp_observables.forEach(value => {

                    scene.onBeforeRenderObservable.remove(value);
                });

            }

            if (temp_GUI.length > 0) {

                temp_GUI.forEach(value => {

                    if (Array.isArray(value)) {
                        value.forEach(value => { if (value.dispose) { value.dispose() } });
                    } else { if (value.dispose) { value.dispose() } }
                });

                temp_GUI = [];

            }

            if (temp_loable_element.Load_Logic) {

                temp_loable_element.Load_Logic();

            }



        }



        var loader = {
            LoadStuff,
            AddDisposableStuff,
            AddObservables,
            AddTemporalGUI,
            Start,
            ClearAll,
            Reset,
            ReloadModels,
            ReloadLogic
        }

        return loader;

    }

    var logicManager = LogicManager(scene);



    return {
        displayPlane,
        container,
        button_previous,
        button_next,
        logicManager,
        addCard,
        Start,
        getCurrentCard,
        isLimitList,
        open,
        close,
        addObserver,
        removeObserver,
        clearObservers,
        clearItems
    }

}





