import React, { Component } from 'react';


export default class WebCam extends Component {

    constructor(props){
        super(props)    

        this.image = null;
        this.state = {
            imageURL: '',
        }
        this.videoEle = React.createRef();
        this.canvasEle = React.createRef();
        this.imageEle = React.createRef();
      
        
    }

    componentDidMount = async () => {
        this.startCamera();
    }

    startCamera = async () => {
        
    const constraints = {
        video: { width: { exact: 640 },
                 height: { exact: 480 },
                 facingMode:"user",
                },
        audio: false
      };


        try {
            const stream =  await navigator.mediaDevices.getUserMedia(constraints);

            this.videoEle.current.srcObject = stream;
            
        } catch(err) {
            console.log(err);
        }
    }


    takeCapture = async () => {
        // Get the exact size of the video element.
        const width = this.videoEle.current.videoWidth;
        const height = this.videoEle.current.videoHeight;

        // get the context object of hidden canvas
        const ctx = this.canvasEle.current.getContext('2d');

        // Set the canvas to the same dimensions as the video.
        this.canvasEle.current.width = width;
        this.canvasEle.current.height = height;

        // Draw the current frame from the video on the canvas.
        ctx.drawImage(this.videoEle.current, 0, 0, width, height);

        // Get an image dataURL from the canvas.
        const imageDataURL = this.canvasEle.current.toDataURL('image/png');
        //this.stopCam();

        this.props.landmarkPicture(imageDataURL);

        

        this.setState({
            imageURL: imageDataURL
        })
    }

   // capture= () =>{
   //     this.props.landmarkPicture(this.canvasEle.current);
   // }

    stopCamera = () => {
        const stream = this.videoEle.current.srcObject;
        const tracks = stream.getTracks();
        
        tracks.forEach(track => {
          track.stop();
        });
    }

    backToCam = () => {
        this.setState({
            imageURL: ''
        }, () => {
            this.startCamera();
        })
    }

  
    

    render() {
        return (
            
          <div className="selfie" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center'}}>

            <div className="cam">
            <video  style={{display:'none'}}  width="100%" height="100%" className="video-player" autoPlay={true} ref={this.videoEle}></video>               
            </div>

            <canvas ref={this.canvasEle} style={{display:'none'}}  ></canvas>
           

           
          </div>
        );
      }

}