import motivational_survey from './motivational_survey.json'

const useStyles = makeStyles(theme => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  toolbarButtons: {
    marginLeft: "auto",
    marginRight: -12
  },
  heroContent: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    //height: '45%',
    paddingBottom: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  cardactionarea: {
    height: '100%'
    //paddingTop:
  },
  cardFooter: {
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[200] : theme.palette.grey[700],
    textAlign: 'center',
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
}));


var surveySample = {
  "completedHtml": "<h3>Gracias por darnos tu comentarios.</h3> <h4>Tus opiniones e ideas nos ayudaran a mejorar, Gracias!</h4>",
  "completedHtmlOnCondition": [
    {
      "expression": "{experience_score} > 8",
      "html": "<h3>Gracias por darnos tu comentarios.</h3> <h4>Nos alegra que te haya gustado tu experiencia en FisicaArtivo!</h4>"
    },
    {
      "expression": "{experience_score} < 7",
      "html": "<h3>Gracias por darnos tu comentarios.</h3> <h4> Nos esforzaremos en mejorar tu experiencia y cumplir con tus expectativas!</h4><br/>"
    }
  ],
  "pages": [
    {
      "name": "page1",
      "elements": [
        {
          "type": "rating",
          "name": "experience_score",
          "title": "En la escala del y al 10 como calificas tu experiencia en FisicaARtivo?",
          "isRequired": true,
          "rateMin": 0,
          "rateMax": 10,
          "minRateDescription": "(No me gust)",
          "maxRateDescription": "(Me gust mucho)"
        },
        {
          "type": "checkbox",
          "name": "experience_features",
          "visible": false,
          "visibleIf": "{experience_score} >= 9",
          "title": "Que caracteristicas consideras como las mas importantes en tu experiencia?",
          "isRequired": true,
          "validators": [
            {
              "type": "answercount",
              "text": "Por favor selecciona 2 caracteristicas maximo",
              "maxCount": 2
            }
          ],
          "choices": [
            "Facilidad de uso",
            "Estabilidad",
            "Material de aprendizaje",
            "Capacidades de interaccion"
          ],
          "hasOther": true,
          "otherText": "Otra:",
          "colCount": 2
        },
        {
          "type": "comment",
          "name": "experience_reason",
          "visible": false,
          "isRequired": true,
          "visibleIf": "{experience_score} > 6  and {experience_score} < 9",
          "title": "Cual es la razon de tu calificacion?"
        },
        {
          "type": "comment",
          "name": "experience_sugestions",
          "visible": false,
          "visibleIf": "{experience_score} notempty",
          "title": "Nos puedes dar algun comentario o sugerencia sobre tu experiencia?"
        }
      ]
    }
  ],
  "showQuestionNumbers": "off"
}



var defaultThemeColors = SurveyJS
  .StylesManager
  .ThemeColors["default"];
defaultThemeColors["$main-color"] = "#5475ec";
defaultThemeColors["$main-hover-color"] = "#2c5bdd";
defaultThemeColors["$text-color"] = "#4a4a4a";
defaultThemeColors["$header-color"] = "#5475ec";

defaultThemeColors["$header-background-color"] = "#4a4a4a";
defaultThemeColors["$body-container-background-color"] = "#f8f8f8";



export default function SurveyPage() {

  const classes = useStyles();

  const { currentUser } = useAuth();

  SurveyJS.StylesManager.applyTheme("default");

  var modelSurvey = new SurveyJS.Model(surveySample);
  //var modelSurvey = new SurveyJS.Model(motivational_survey);
  var survey_name = "survey_retro_students"

  function checkUserSurveyData() {

    if (currentUser) {
      firestore.collection('users').doc(currentUser.uid).collection('surveys').doc('survey_1').get().then(response => {


        if (response.exists) {


        }


      }, (error => {
        console.log(error.message)
      }))
    }
  }


  function sendDataToServer() {
    modelSurvey.onComplete.add(result => {
      if (currentUser) {

        var dateObj = new Date();
        var month = dateObj.getUTCMonth() + 1; //months from 1-12
        var day = dateObj.getUTCDate();
        var year = dateObj.getUTCFullYear();
        var hours = dateObj.getUTCHours();
        var minutes = dateObj.getUTCMinutes();

        var doctag = (survey_name + "-" + year + "-" + month + "-" + day + " " + hours + ":" + minutes).toString();
        firestore.collection('users').doc(currentUser.uid).collection('surveys').doc(doctag).set(result.data);
      }
    });

  }

  return (
    <React.Fragment>
      <AppBar position="relative">
        <Toolbar>
          <BallotIcon className={classes.icon} />
          <Typography className={classes.title} variant="h6" color="inherit" noWrap>
            Encuesta de experiencia
          </Typography>



        </Toolbar>
      </AppBar>
      <Container className={classes.heroContent} >
        <Typography variant="h5" align="center" color="textSecondary"> Por favor ayudanos respondiendo esta corta encuesta.</Typography>
        <SurveyJS.Survey model={modelSurvey} onComplete={sendDataToServer} />
      </Container>
    </React.Fragment>

  );
}