import React, {useRef,useState,useEffect} from 'react';

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';

import { Link as RouterLink, withRouter } from 'react-router-dom';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import MenuItem from '@material-ui/core/MenuItem';
import Alert from '@material-ui/lab/Alert';

import {useAuth} from '../Contexts/AuthContext'
import {useHistory } from "react-router-dom"
import {firestore} from '../firebase'


const MyLink = React.forwardRef((props, ref) => <RouterLink innerRef={ref} {...props} />);



function Copyright() {
  return (
    
    <Typography variant="body2" color="textSecondary" align="center">
        {'Division de estudios de '}
      <Link color="inherit" href="https://material-ui.com/">
        posgrado
      </Link>
      {' e investigacion.'}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function UpdateProfile() {
  const classes = useStyles();

  
  const emailRef = useRef();
  const passwordRef = useRef();
  const confirmPasswordRef = useRef();
  const fNameRef = useRef();
  const lNameRef = useRef();
  const birthdayRef = useRef();
  const sexRef = useRef();

  const {currentUser,updatePassword, updateEmail} = useAuth();
  const [error, setError] = useState('');
  const [loading,setLoading] = useState(false);
  const [userData,setUserData] = useState({email:'',fname:'',lname:'',sex:'',birthday:''});
  const history = useHistory();

  const handleChange = (e) => {
    setUserData({
      ...userData,
      [e.target.name]: e.target.value
    });
  };

    

   function handleSubmit(e) {
    e.preventDefault();

    if(passwordRef.current.value !== confirmPasswordRef.current.value){
      return setError('Las contraseas no coinciden')
    }

    const promises=[]
    setLoading(true)
    setError('')

    if (emailRef.current.value !== currentUser.email){
        promises.push(updateEmail(emailRef.current.value))
        promises.push(firestore.collection('users').doc(currentUser.uid).update({email:emailRef.current.value}))


    }
    if(passwordRef.current.value){
        promises.push(updatePassword(passwordRef.current.value))
    }

    promises.push(firestore.collection('users').doc(currentUser.uid).update({
      fname: fNameRef.current.value,
      lname:lNameRef.current.value,
      sex:sexRef.current.value,
      birthday:birthdayRef.current.value,
    }))

    Promise.all(promises).then(()=>{
        history.push('/excercises')        
    }).catch(()=>{
        setError("la actualizacion del perfil de usuario ha fallado")
    }).finally(()=>{
        setLoading(false)
    })

  }

  useEffect(() => {
   // setTimeout(() => {    
      if(currentUser){
        firestore.collection('users').doc(currentUser.uid).get().then(response =>{
         setUserData(response.data())
        },(error =>{
          console.log(error.message)
        }))
      }

    //}, 1000);
    // eslint-disable-next-line
  }, []);

  

  return (
  
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
         Editar Cuenta
        </Typography>
        
        {error && <Alert severity="error">{error}</Alert>}
        <form onSubmit={handleSubmit} className={classes.form} >
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="fname"
                name="fname"
                variant="outlined"
                //required
                fullWidth
                id="fname"
                label="fname"
                autoFocus
               inputRef={fNameRef}
               value={userData.fname}
               onChange={handleChange}
               disabled={true}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                variant="outlined"
                //required
                fullWidth
                id="lname"
                label="Apellidos"
                name="lname"
                autoComplete="lname"
                inputRef={lNameRef}
                value={userData.lname}
                onChange={handleChange}
                disabled={true}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="email"
                label="Correo electronico"
                type="email"
                name="email"
                autoComplete="email"
                inputRef={emailRef}
                value={userData.email}
                onChange={handleChange}
                disabled={false}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="date"
                label="fecha de nacimiento (DD/MM/AAAA)"
                type="date"
                name="birthday" 
                InputLabelProps={{
                  shrink: true,
                }}              
                inputRef={birthdayRef}
                value={userData.birthday}
                onChange={handleChange}
                disabled={true}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                id="Sex"
                select
                required
                fullWidth
                label="Sexo"
                name="sex"            
                variant="outlined"
                inputRef={sexRef}
                value={userData.sex}
                onChange={handleChange}
                disabled={true}
              >
                <MenuItem value={"Masculino"}>Masculino</MenuItem>
                <MenuItem value={"Femenino"}>Femenino</MenuItem> 
              </TextField>
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"               
                fullWidth
                name="password"
                label="Contrasea"
                type="password"
                id="password"
                autoComplete="current-password"
                inputRef={passwordRef}
                placeholder="Dejar en blanco para mantener contrasea."
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"              
                fullWidth
                name="confirmpassword"
                label="Comfirma contrasea"
                type="password"
                id="confirmpassword"
                inputRef={confirmPasswordRef}
                placeholder="Dejar en blanco para mantener contrasea."
                
              />
            </Grid>
          </Grid>
          <Button
            disabled={loading}
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Aplicar cambios
          </Button>

          <Button
            fullWidth
            variant="contained"
            color="primary"            
            component={RouterLink} to={"/excercises"}
          >
           Cancelar
          </Button>
       
        </form>
      </div>
      <Box mt={5}>
        <Copyright />
      </Box>
    </Container>
   
  );
}
