import * as BABYLON from 'babylonjs'
import * as GUI from 'babylonjs-gui';

export class ImageInfoCard {
    constructor(name, src) {
        this.name = name;
        this.src = src;
        this.Base_image = new GUI.Image(name, src);
        this.Base_image.zIndex=-1;
        this.info_image_container = new GUI.Container(name)

        this.info_image_container.addControl(this.Base_image)

    }

    addLogic(logic=function(){}) {
        this.Load_Logic = logic
    }
    addModels(models=async function(){}) {
        this.Load_Models = models
    }

}

export class ContainerInfoCard {
    constructor(name, container) {
        this.name = name;
        this.info_image_container = container;
        

    }

    addLogic(logic=function(){}) {
        this.Load_Logic = logic
    }
    addModels(models=async function(){}) {
        this.Load_Models = models
    }

}



