import React, {useRef,useState} from 'react';

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { Link as RouterLink, withRouter } from 'react-router-dom';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';


import Alert from '@material-ui/lab/Alert';

import AppBar from '@material-ui/core/AppBar';
import IconButton from '@material-ui/core/IconButton';
import HomeIcon from '@material-ui/icons/Home';

import {useAuth} from '../Contexts/AuthContext'


const MyLink = React.forwardRef((props, ref) => <RouterLink innerRef={ref} {...props} />);



function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright  '}
      <Link color="inherit" href="https://material-ui.com/">
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  toolbarButtons: {
    marginLeft: "auto",
    marginRight: 5
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignUp() {
  const classes = useStyles();

  
  const emailRef = useRef();
  const passwordRef = useRef();
  const confirmPasswordRef = useRef();
  const fNameRef = useRef();
  const lNameRef = useRef();
  const birthdayRef = useRef();
  const sexRef = useRef();

  const [sex, setSex] = useState('');

  const handleSexChange = (event) => {
    setSex(event.target.value);
  };


  const {signup} = useAuth();
  const [error, setError] = useState('');
  const [loading,setLoading] = useState(false);

  async function handleSubmit(e) {
    e.preventDefault();

    if(passwordRef.current.value !== confirmPasswordRef.current.value){
      return setError('Las contraseas no coinciden')
    }

    try{
      setError('')
      setLoading(true)
      
      await signup(emailRef.current.value, passwordRef.current.value, fNameRef.current.value,lNameRef.current.value,birthdayRef.current.value,sexRef.current.value)
    }catch{
      setError('Falla al crear la cuenta')
    }

    setLoading(false)
  }


  

  return (
  
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <AppBar
        position="fixed"
      >        
          <IconButton 
          className={classes.toolbarButtons} 
          color="inherit" 
          aria-label="Back to home" 
          onClick={()=>(window.location="/")}>
          <HomeIcon />
          </IconButton>
      </AppBar>
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Registrate
        </Typography>
        
        {error && <Alert severity="error">{error}</Alert>}
        <form onSubmit={handleSubmit} className={classes.form} >
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="fname"
                name="firstName"
                variant="outlined"
                required
                fullWidth
                id="firstName"
                label="Nombre"
                autoFocus
               inputRef={fNameRef}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="lastName"
                label="Apellidos"
                name="lastName"
                autoComplete="lname"
               inputRef={lNameRef}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="date"
                label="fecha de nacimiento (DD/MM/AAAA)"
                type="date"
                name="date" 
                InputLabelProps={{
                  shrink: true,
                }}              
                inputRef={birthdayRef}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                id="Sex"
                select
                required
                fullWidth
                label="Sexo"
                //value={sex}
                //onChange={handleSexChange}               
                variant="outlined"
                inputRef={sexRef}
              >
                <MenuItem value={"Masculino"}>Masculino</MenuItem>
                <MenuItem value={"Femenino"}>Femenino</MenuItem> 
              </TextField>
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="email"
                label="Correo electronico"
                type="email"
                name="email"
                autoComplete="email"
                inputRef={emailRef}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="password"
                label="Contrasea"
                type="password"
                id="password"
                autoComplete="current-password"
                inputRef={passwordRef}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="confirmpassword"
                label="Comfirma contrasea"
                type="password"
                id="confirmpassword"
                inputRef={confirmPasswordRef}
                
              />
            </Grid>
          </Grid>
          <Button
            disabled={loading}
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Registrate
          </Button>
          <Grid container justify="flex-end">
            <Grid item>
              <Link to="/login" component={MyLink} variant="body2">
              Ya tienes una cuenta? Accede.
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={5}>
        <Copyright />
      </Box>
    </Container>
   
  );
}
