//https://github.com/mui-org/material-ui/tree/master/docs/src/pages/getting-started/page-layout-examples
import React, {useRef,useState} from 'react';

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

import { Link as RouterLink} from 'react-router-dom';
import {useHistory } from "react-router-dom"
import {Redirect} from 'react-router-dom'

import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import AppBar from '@material-ui/core/AppBar';
import IconButton from '@material-ui/core/IconButton';
import HomeIcon from '@material-ui/icons/Home';

import Alert from '@material-ui/lab/Alert';

import {useAuth} from '../Contexts/AuthContext'

const MyLink = React.forwardRef((props, ref) => <RouterLink innerRef={ref} {...props} />);


function Footer() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Proyecto FisicARctivo'}
      <br />
      <Link color="inherit" href="https://www.culiacan.tecnm.mx/">
        Tecnolgico Nacional De Mexico Campus Culicn
      </Link> 
      <br />
      {'\n'}
      <Link color="inherit" href="https://conacyt.mx/">
        CONACYT 2021
      </Link>      
    </Typography>
  );
}

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  toolbarButtons: {
    marginLeft: "auto",
    marginRight: 5
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function Login() {
  const classes = useStyles();

  const emailRef = useRef();
  const passwordRef = useRef();

  const {login} = useAuth();
  const [error, setError] = useState('');
  const [loading,setLoading] = useState(false);
  const history = useHistory();


  async function handleSubmit(e) {
    e.preventDefault();

  
    try{
      setError('')
      setLoading(true)
      
      await login(emailRef.current.value,passwordRef.current.value)
      //history.push("/excercises")
    }catch{
      setError('Falla al intentar ingresar a la pagina')
    }

    setLoading(false)
  }


  return (
    <Container component="main" maxWidth="xs">        
      <CssBaseline />
      <AppBar
        position="fixed"
      >        
          <IconButton 
          className={classes.toolbarButtons} 
          color="inherit" 
          aria-label="Back to home" 
          onClick={()=>(window.location="/")}>
          <HomeIcon />
          </IconButton>
      </AppBar>
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Ingresar a FisicARtivo
        </Typography>
        {error && <Alert severity="error">{error}</Alert>}
        <form className={classes.form} onSubmit={handleSubmit}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Correo electronico"
            name="email"
            autoComplete="email"
            autoFocus
            inputRef={emailRef}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Contrasea"
            type="password"
            id="password"
            autoComplete="current-password"
            inputRef={passwordRef}
          />
          <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Recuerdame"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            disabled={loading}
            
          >
            Ingresar
          </Button>
         
          <Grid container>
            <Grid item xs>
              <Link to="/forgot-password"  component={MyLink} variant="body2">
                Olvide mi contrasea
              </Link>
            </Grid>
            <Grid item>
              <Link to="/signUp" component={MyLink} variant="body2">
                {"No tienes una cuenta? Registrate"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={5}>
        <Footer />
      </Box>
    </Container>
  );
}