            // FUNCTIONS FOR GUI COMPONENTS CREATION
            function arrowForce(arrowStart, arrowDirection, scale, length, data, datarotation, mesh, force) {

                var CoT = new BABYLON.TransformNode("root");

                //Shape profile in XY plane
                const myShape = [];
                const arrowRadius = 0.5;
                var n = 30;
                var deltaAngle = 2 * Math.PI / n
                for (let i = 0; i <= n; i++) {
                    myShape.push(new BABYLON.Vector3(arrowRadius * Math.cos(i * deltaAngle), arrowRadius * Math.sin(i * deltaAngle), 0))

                }
                myShape.push(myShape[0]);  //close profile

                const arrowHeadLength = 1.5;
                const arrowHeadMaxSize = 1.5;
                const arrowLength = 10;
                const arrowBodyLength = arrowLength - arrowHeadLength;
                //var arrowStart = new BABYLON.Vector3(0, 0, 0);
                //var arrowDirection = new BABYLON.Vector3(1, 0, 0);
                arrowDirection.normalize();

                const arrowBodyEnd = arrowStart.add(arrowDirection.scale(arrowBodyLength));
                const arrowHeadEnd = arrowBodyEnd.add(arrowDirection.scale(arrowHeadLength))

                const myPath = [];
                myPath.push(arrowStart);
                myPath.push(arrowBodyEnd);
                myPath.push(arrowBodyEnd)
                myPath.push(arrowHeadEnd);

                const scaling = (index, distance) => {
                    switch (index) {
                        case 0:
                        case 1:
                            return 1
                            break
                        case 2:
                            return arrowHeadMaxSize / arrowRadius
                            break
                        case 3:
                            return 0
                            break
                    }
                };

                let arrow = BABYLON.MeshBuilder.ExtrudeShapeCustom("arrow", { shape: myShape, path: myPath, updatable: true, scaleFunction: scaling, sideOrientation: BABYLON.Mesh.DOUBLESIDE }, scene);

                //arrow.scalingDeterminant=0.2

                var button1 = {}

                const arrowLengthbyScale = (arrow, shape, path, length) => {
                    const arrowHeadLength = path[3].subtract(path[2]).length();
                    const arrowLength = path[3].subtract(path[0]).length() * length;
                    const arrowBodyLength = arrowLength - arrowHeadLength;
                    const arrowStart = path[0];

                    let arrowDirection = path[1].subtract(path[0]);
                    arrowDirection.normalize();

                    const arrowBodyEnd = arrowStart.add(arrowDirection.scale(arrowBodyLength));
                    const arrowHeadEnd = arrowBodyEnd.add(arrowDirection.scale(arrowHeadLength))

                    path[1] = arrowBodyEnd;
                    path[2] = arrowBodyEnd;
                    path[3] = arrowHeadEnd;

                    BABYLON.MeshBuilder.ExtrudeShapeCustom("arrow", { shape: shape, path: path, scaleFunction: scaling, instance: arrow }, scene);

                    var middlepoint = arrowBodyEnd;
                    middlepoint.x = (middlepoint.x / 2)
                    middlepoint.y = (middlepoint.y / 2)
                    middlepoint.z = (middlepoint.z / 2)

                    // GUI

                    var plane = BABYLON.MeshBuilder.CreatePlane("planearrow", {
                        width: 3,
                        height: 3
                    }, scene);

                    plane.nonXREditable = true;

                    //var matrix = mesh.computeWorldMatrix(true);  // force calculation of world matrix
                    //var local_pos = new BABYLON.Vector3(-0.1, 1.2, 0); //top middle of box relative to box
                    //var global_pos = BABYLON.Vector3.TransformCoordinates(local_pos, matrix); //calculate world position
                    //plane.position = local_pos; //position sphere relative to world
                    plane.rotation.y = (datarotation * (Math.PI / 180))


                    plane.parent = arrow;
                    plane.scalingDeterminant = length;

                    plane.position = middlepoint;
                    plane.position.y = 2 * length;

                    var advancedTexture = GUI.AdvancedDynamicTexture.CreateForMesh(plane);

                    button1 = GUI.Button.CreateSimpleButton("but1", data);
                    button1.width = 3;
                    button1.height = 3;
                    button1.color = "white";
                    button1.fontSize = 400;
                    button1.background = "green";
                    button1.onPointerUpObservable.add(function () {

                        // alert("you did it!");
                        impulsewTransform(mesh);

                    });
                    advancedTexture.addControl(button1);
                    //


                }

                //Impulse Settings
                var impulseDirection = arrowDirection;
                var impulseMagnitude = force;
                var contactLocalRefPoint = BABYLON.Vector3.Zero();

                var transformForce = function (mesh, vec) {
                    var mymatrix = new BABYLON.Matrix();
                    mesh.rotationQuaternion.toRotationMatrix(mymatrix);
                    return BABYLON.Vector3.TransformNormal(vec, mymatrix);
                };

                var editForce = function (force) {
                    impulseMagnitude = force;
                }

                var impulse = function (mesh) {

                    mesh.physicsImpostor.applyImpulse(impulseDirection.scale(impulseMagnitude), mesh.getAbsolutePosition().add(contactLocalRefPoint));


                }

                var impulsewTransform = function (mesh) {

                    mesh.physicsImpostor.applyImpulse(transformForce(mesh, impulseDirection).scale(impulseMagnitude), mesh.getAbsolutePosition().add(contactLocalRefPoint));


                }

                arrowLengthbyScale(arrow, myShape, myPath, length);


                if (mesh) {

                    //arrow.scalingDeterminant=0.2 //NO FUNCA

                    //arrow.parent =mesh;
                    //CoT.parent =mesh;

                    //arrow.parent= CoT;
                    //CoT.scalingDeterminant=0.2;

                    arrow.parent = mesh;
                    arrow.scaling = new BABYLON.Vector3(scale / mesh.scaling.x, scale / mesh.scaling.y, scale / mesh.scaling.z);
                    arrow.position = arrowStart;




                }

                function activate() {

                    impulsewTransform(mesh);
                }

                var returnee = {
                    activate: activate,
                    button: button1,
                    editForce: editForce,

                }


                return returnee;
            }
