            function isCompleted_Question_number() {

                var currentQuiz = 'quiz_' + (loaded_exercise_index + 1)

                if (DataCapture.getQuizByID(currentQuiz).isCompleted) {
                    return " (Completada)"
                } else {
                    return ""
                }

            }
            function Check_Errors_number() {
                var currentQuiz = 'quiz_' + (loaded_exercise_index + 1)

                return DataCapture.getQuizByID(currentQuiz).errors;
            }
            function Check_Timer_number() {

                if (DataCapture.getExerciseData().isCompleted) {
                    OnscreenTimer.pause();
                    return formatTime(OnscreenTimer.getTimeValues().minutes) + ":" + formatTime(OnscreenTimer.getTimeValues().seconds) + "\n" + "Seccin de preguntas finalizada!"
                } else {
                    return formatTime(OnscreenTimer.getTimeValues().minutes) + ":" + formatTime(OnscreenTimer.getTimeValues().seconds)
                }
            }
            function formatTime(time) {
                if (time <= 9) {
                    return "0" + time;
                } else {
                    return time;
                }
            }

