            async function loadBlueCarModel() {

                function rotatewithgrades(mesh, grades) {

                    mesh.rotationQuaternion = new BABYLON.Quaternion.RotationAxis(new BABYLON.Vector3(0, 1, 0), (grades * (Math.PI / 180)));
                }


                function attachToCollider(modelMeshes, rootMesh, collider, BABYLON_Impostor_Type) {

                    var boundingMinMax = modelMeshes[rootMesh].getHierarchyBoundingVectors();
                    var bounds = boundingMinMax.max.subtract(boundingMinMax.min);
                    var geometricCenter = boundingMinMax.max.add(boundingMinMax.min).scale(0.5);

                    collider.scaling.copyFrom(bounds);
                    collider.XRPickable = true;
                    collider.visibility = 0;

                    collider.physicsImpostor = new BABYLON.PhysicsImpostor(collider, BABYLON_Impostor_Type, { mass: 25 }, scene);

                    collider.position = geometricCenter;

                    modelMeshes[rootMesh].setParent(collider);




                }

                var defaultmeshes = [];


                var carmodel = (await BABYLON.SceneLoader.ImportMeshAsync("", "../3DModels/", "bluecar.glb", scene));

                var carmesh = carmodel.meshes;

                carmesh[0].scaling.scaleInPlace(0.1);
                carmesh[0].position.set(-.5, .33, 0);


                //busmeshes[0].rotationQuaternion=null;
                //busmeshes[0].rotation.y=(45*(Math.PI/180))                
                //busmeshes[0].rotationQuaternion = new BABYLON.Quaternion.RotationAxis(new BABYLON.Vector3(0, 1, 0), 0 );                
                //busmeshes[7].showBoundingBox = true;            

                var carcollider = BABYLON.MeshBuilder.CreateBox("carBox1", { height: carmesh[1].height, width: carmesh[1].width, depth: carmesh[1].depth }, scene);

                attachToCollider(carmesh, 0, carcollider, BABYLON.PhysicsImpostor.BoxImpostor);

                //GIRA CON CUARTERNION NO CON .ROTACION (FUNCIONES EULER)

                rotatewithgrades(carcollider, 180);

                //buscollider.rotationQuaternion = new BABYLON.Quaternion.RotationAxis(new BABYLON.Vector3(0, 1, 0), (90*(Math.PI/180)) );                


                defaultmeshes.push(carmesh);
                defaultmeshes.push(carcollider);

                section_infodisplay.logicManager.AddDisposableStuff(carmesh);
                section_infodisplay.logicManager.AddDisposableStuff(carcollider);



                return defaultmeshes;

            }

