import logo from './logo.svg';
import './App.css';


import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import PrivateRoute from './WebComponents/PrivateRoute'
import RedirectRoute from './WebComponents/RedirectRoute'

import { AuthProvider } from './Contexts/AuthContext';

import Intro from './Webpages/Intro'
import Login from './Webpages/Login';
import SignUp from './Webpages/SignUp'
import ForgotPasword from './Webpages/Forgot-Password'

import UserProfile from './Webpages/UserProfile'
import ExerciseList from './Webpages/ExerciseList';

import SurveyPage_Teacher from './Webpages/TeachersSurveyPage'
import SurveyPage_Student from './Webpages/StudentsSurveyPage'

import * as exer_1_1 from './ExcercisePage/exercise_1_1/index';
import * as exer_1_2 from './ExcercisePage/exercise_1_2/index';
import * as exer_1_3 from './ExcercisePage/exercise_1_3/index';
import * as exer_1_4 from './ExcercisePage/exercise_1_4/index';
import * as exer_2_1 from './ExcercisePage/exercise_2_1/index';
import * as exer_2_2 from './ExcercisePage/exercise_2_2/index';
import * as exer_2_3 from './ExcercisePage/exercise_2_3/index';
import * as exer_2_4 from './ExcercisePage/exercise_2_4/index';

import * as compatibility_test from './ExcercisePage/test_compatibility/index'

import * as test_comp_0_0 from './ExcercisePage/test_comp_0_0/index'




function App() {
  return (
   
     <Router>
     <AuthProvider>
       <Switch>
          <Route exact path="/" component={Intro}/> 

          <RedirectRoute path="/login" component={Login} />
          <RedirectRoute path="/signUp" component={SignUp} />
          <RedirectRoute path="/forgot-password" component={ForgotPasword} />

          <PrivateRoute path="/excercises" component={ExerciseList} />  
          <PrivateRoute exact path="/userprofile" component={UserProfile}></PrivateRoute>
          <PrivateRoute exact path="/survey-teacher" component={SurveyPage_Teacher}></PrivateRoute>
          <PrivateRoute exact path="/survey-student" component={SurveyPage_Student}></PrivateRoute>

          <PrivateRoute path="/1_1" component={exer_1_1.default} ></PrivateRoute>  
          <PrivateRoute path="/1_2" component={exer_1_2.default} ></PrivateRoute> 
          <PrivateRoute path="/1_3" component={exer_1_3.default} ></PrivateRoute>  
          <PrivateRoute path="/1_4" component={exer_1_4.default} ></PrivateRoute> 
          <PrivateRoute path="/2_1" component={exer_2_1.default} ></PrivateRoute> 
          <PrivateRoute path="/2_2" component={exer_2_2.default} ></PrivateRoute> 
          <PrivateRoute path="/2_3" component={exer_2_3.default} ></PrivateRoute> 
          <PrivateRoute path="/2_4" component={exer_2_4.default} ></PrivateRoute> 
          <PrivateRoute path="/test" component={test_comp_0_0.default} ></PrivateRoute> 

          <Route exact path="/compatibility_test" component={compatibility_test.default}/> 
        

       </Switch>

      </AuthProvider>
     </Router>
   
  );
}

export default App;
