import * as BABYLON from 'babylonjs'
import * as GUI from 'babylonjs-gui';

export default function XRControlDisplay(scene, size, XRPickable) {

    // WINDOW PLANE FOR CONTROL AND POSITION

    var windowMesh = new BABYLON.MeshBuilder.CreatePlane("windown_plane", {
        width: size * (0.8),
        height: size * (0.15),
        sideOrientation: BABYLON.Mesh.DOUBLESIDE
    }, scene);

    windowMesh.XRPickable = XRPickable;
    windowMesh.nonXREditable = true;

    var window_texture = new GUI.AdvancedDynamicTexture.CreateForMesh(windowMesh);
    window_texture.scaleTo(400,100)

    var win_rectangle = new GUI.Rectangle("container")
    win_rectangle.width = 1;
    win_rectangle.height = 1;
    
    //container.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_CENTER;
    //container.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_TOP;
   
    //container.cornerRadius = 20;
    //container.color = "Orange";
    win_rectangle.thickness = 0;
    win_rectangle.background = '#00000066';
    
    //container.paddingTopInPixels = 15;
    //container.paddingLeftInPixels = 15;
    //container.zIndex=-1;

    
    var win_grid = new GUI.Grid("win_grid")
    win_grid.addRowDefinition(1);
    win_grid.addColumnDefinition(.7)
    win_grid.addColumnDefinition(.15)
    win_grid.addColumnDefinition(.15)

    win_grid.paddingLeftInPixels=5;
    

    const win_text_name = new GUI.TextBlock("win_text_name");
    win_text_name.fontFamily = "Helvetica";
    //win_text_name.textWrapping = true;
    win_text_name.text = "Pantalla de controles"
    win_text_name.color = "white";
    win_text_name.fontSize = 28;
    win_text_name.height = "100px";   
    win_text_name.textHorizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_LEFT
   

    win_grid.addControl(win_text_name,0,0);


    win_rectangle.addControl(win_grid);
    

    window_texture.addControl(win_rectangle);


    //DISPLAY PLANE FOR CONTENT
    
    var displayPlane = new BABYLON.MeshBuilder.CreatePlane("displayPlaneinfo", {
        width: size * (0.8),
        height: size * (0.8)
    }, scene);

    var local_pos = new BABYLON.Vector3(-0.7, .5, 0);
    windowMesh.position = local_pos
    windowMesh.rotation.y = (90 * (Math.PI / 180))
    
    
    var ajust_pos=displayPlane.getBoundingInfo().boundingBox.maximum.y +windowMesh.getBoundingInfo().boundingBox.maximum.y
    var change_axis = new BABYLON.Vector3(0, -ajust_pos, 0);
    
    displayPlane.parent = windowMesh;
    displayPlane.position = change_axis;


    //var matrix = mesh.computeWorldMatrix(true);  // force calculation of world matrix   
    //var local_pos = new BABYLON.Vector3(-0.1, 1.2, 0); //top middle of box relative to box
    //var global_pos = BABYLON.Vector3.TransformCoordinates(local_pos, matrix); //calculate world position
    
    //displayPlane.nonXREditable = true;
    //displayPlane.XRPickable = XRPickable;
    //displayPlane.billboardMode = BABYLON.Mesh.BILLBOARDMODE_ALL 
    

    var container_texture = new GUI.AdvancedDynamicTexture.CreateForMesh(displayPlane);
    container_texture.scaleTo(300,300)
    //container_texture.idealWidth = 800;
    //container_texture.idealHeight = 600;
    //container_texture.useSmallestIdeal = true


    var container = new GUI.Rectangle("container")
    container.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_RIGHT;
    container.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_TOP;
    container.width = 1;
    //container.height = "250px";
    //container.cornerRadius = 20;
    //container.color = "Orange";
    container.thickness = 0;
    //container.background = "green";
    //container.paddingTopInPixels = 15;
    //container.paddingRightInPixels = 15;
    container_texture.addControl(container);



    var panel = new GUI.StackPanel("panel");
    container.addControl(panel);
    panel.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_RIGHT;
    panel.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_TOP;
    



    var controlPanel = new GUI.SelectionPanel("spi");
    controlPanel.width = 1;
    controlPanel.height = "170px";
    controlPanel.background = "#1388AF";
    controlPanel.thickness = 0;
    controlPanel.fontSize = 17;


    controlPanel.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
    controlPanel.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_TOP;


    panel.addControl(controlPanel);


    var button = GUI.Button.CreateSimpleButton("but", "Ejecutar");
    //button.width = 0.5;
    button.height = "40px";
    button.color = "white";
    button.fontSize = 20;
    button.background = "#1388AF";
    //button.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
    //button.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_CENTER;               
    //button.top = "-10px";

    //button.onPointerClickObservable.add(ExecuteButton);
    panel.addControl(button);


    var button2 = GUI.Button.CreateSimpleButton("but", "Reiniciar");
    //button.width = 0.5;
    button2.height = "40px";
    button2.color = "white";
    button2.fontSize = 20;
    button2.background = "#1388AF";
    //button.horizontalAlignment = GUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
    //button.verticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_CENTER;               
    //button.top = "-10px";

    //button.onPointerClickObservable.add(ExecuteButton);
    panel.addControl(button2);


    function open() {


        var openKeys = [];
        openKeys.push({
            frame: 0,
            value: 0 //BABYLON.Vector2.Zero()
        });
        openKeys.push({
            frame: 10,
            value: 1 // new BABYLON.Vector2(1, 1)
        });

        var animationOpen = new BABYLON.Animation(
            'scaleAnimationOpen',
            'scaleX',
            30,
            BABYLON.Animation.ANIMATIONTYPE_FLOAT, //VECTOR2,
            BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT
        );
        animationOpen.setKeys(openKeys);

        let easingFunction = new BABYLON.BounceEase(1, 4);
        easingFunction.setEasingMode(BABYLON.EasingFunction.EASINGMODE_EASEOUT);
        animationOpen.setEasingFunction(easingFunction);

        //container.animations.push(animationOpen);

        //scene.beginAnimation(container, 0, 100, false, 1);
        //scene.beginDirectAnimation(container, [animationOpen], 0, openKeys[openKeys.length-1].frame, false, 1);
        scene.beginDirectAnimation(container, [animationOpen], 0, 100, false, 1);

    }

    function close() {


        var keys = [];
        keys.push({
            frame: 0,
            value: 1
        });
        keys.push({
            frame: 10,
            value: 0
        });


        var animationClose = new BABYLON.Animation(
            'scaleAnimation',
            'scaleX',
            10,
            BABYLON.Animation.ANIMATIONTYPE_FLOAT,
            BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT
        );
        animationClose.setKeys(keys);

        let easingFunction = new BABYLON.ExponentialEase(9.7); // BABYLON.QuarticEase()
        easingFunction.setEasingMode(BABYLON.EasingFunction.EASINGMODE_EASEINOUT);
        animationClose.setEasingFunction(easingFunction);

        //container.animations.push(animationClose);

        //scene.beginAnimation(container, 0, 100, false, 1, () => {
        //menuPlane.dispose();
        //});

        scene.beginDirectAnimation(container, [animationClose], 0, 100, false, 1);

    }

    var observables_list = [];

    function addObserver(params) {

        observables_list.push(params);

    }

    function removeObserver(number) {
        button.onPointerUpObservable.remove(observables_list[number])
        observables_list.splice(number, 1);
    }

    function clearObservers() {
        button.onPointerUpObservable.clear();
        observables_list = []
    }

    function clearItems() {

        controlPanel.height = "2px"
        var number = controlPanel.groups.length - 1;
        for (var i = 0; i <= number; i++) {
            controlPanel.removeGroup(i);
        }

    }



    return {
        displayPlane:windowMesh,
        container,
        button,
        button2,
        controlPanel,
        open,
        close,
        addObserver,
        removeObserver,
        clearObservers,
        clearItems
    }

}
