



export class exercise {
    constructor(questionInput = "", answersInput = [""], correctanswerInput = 0, extraDataInput = [""]) {

        this.quiz = {
            question:questionInput,
            answers:answersInput,
            correctanswer:answersInput[correctanswerInput],
            extradata:extraDataInput
        }

    }

    addLogic(logic=function(){}) {
        this.Load_Logic = logic
    }
    addModels(models=async function(){}) {
        this.Load_Models = models
    }


}


export var quiz1 = new exercise("actividad: Un automovilista viaja en una carretera recta a una velocidad constante de 90Km/h. Determinar la distancia que recorre en 5 minutos", ["A) 7.5 km", "B) 10.4 Km", "C) 5400 m", "D) 7200 m"], 0);
