        var loaded_exercise_index = 0;

        // CODE SNIPLET FOR CONTROL OF GLOBAL SCALE OF STUFF IN THE SCENE (SUPER IMPORTANT)

        //rootTnode for rotation
        const rootNode = new BABYLON.TransformNode("Root Container", scene);


        //rootNode.scaling = new Vector3(scale, scale, scale); <= code to scale everything 
        //rootNode.rotate(Vector3.Down(), (touchEvent.currentState - touchEvent.previousState) * 0.005); <= code to rotate everything

        // TODO: Figure out why getFrontPosition stopped working
        //box.position = (scene.activeCamera as TargetCamera).getFrontPosition(2);

